import { UserDataQuota } from "./user-data-quota.types"

type ApplicationEntitlement =
  | UserDataQuota["featureAccessPolicies"][number]
  | `content:${UserDataQuota["contentAccessPolicies"][number]}`

const products = ["pro", "financial-data", "jobs", "content-boost"] as const
type Product = typeof products[number]

const isValidProduct = (unknownProduct: string | undefined): unknownProduct is Product => {
  return typeof unknownProduct === "string" && products.includes(unknownProduct as Product)
}

export { isValidProduct }
export type { Product, ApplicationEntitlement }
